import { MyDate } from './mydate';

export class Package {

    constructor(public id: string,
        public title: string,
        public info: Object,
        public company: Object, //number
        public image_url: string,
        public categories: string,
        public state: Object,
        public place: string,
        public date: MyDate,
        public type: string,
        public priority: number,
        public is_special: boolean,
        public is_avaliable: boolean,
        public createdAt: string,
        public updatedAt: string) {

    }

    static formArray({ id, title, info, company, image_url, categories, state, place, date, type, priority, is_avaliable, is_special, createdAt, updatedAt }): Package {
        return new Package(id, title, info, company, image_url, categories, state, place, date, type, priority, is_special, is_avaliable, createdAt, updatedAt);
    }

    static forListing(data: any):Object {


        let myObj = {
            id : data.id,
            title : data.title,
            price : data.info.quick_info.price, 
            image_url : data.image_url[0],
            currency : data.info.quick_info.currency,
            date :  data.date[0]['start_date'],
            // is_special : data
            //priority : boolean;
            duration : data.info.quick_info.duration
        }
        console.log(myObj);
        return myObj;
    }

    static forDetailPage(data: any): Object {
        console.log(data);
        console.log(data.title);
        // var myObj=  new Object();
        // myObj['title'] = data.title;
        // myObj['price'] = data.info.quick_info.price;
        // myObj['currency'] = data.info.quick_info.currency;
        // myObj['duration'] = data.info.quick_info.duration;
        // // myObj['car_type'] = data.quick_info.car_type;
        // // myObj['hotel_type'] = data.quick_info.hotel_type;
        // // myObj['hotel_type'] = data.quick_info.hotel_type;
        // myObj['image_url'] = data.image_url[0],
        let services: string = "";
        for (var i in data.info.detail_info.services) {
            services += data.info.detail_info.services[i];
            if ((parseInt(i) + 1) !== data.info.detail_info.services.length) {
                services += " , ";
            }
        }

        let myObj = {
            title: data.title,
            image_url: data.image_url[0],
            price: data.info.quick_info.price,
            currency: data.info.quick_info.currency,
            date: data.date[0].start_date,
            package_info: data.info.detail_info.intro,
            duration: data.info.quick_info.duration,
            service: data.info.detail_info.services,
            hote_type: data.info.quick_info.hotel_type,
            route: data.info.quick_info.route,
            car_type: data.info.quick_info.car_type,
            services: services,
            plans: data.info.detail_info.plan
        };
        console.log(myObj);
        console.log("R I P")

        return myObj;
    }



}



