import { Injectable } from '@angular/core'
import { Http, Jsonp, RequestOptions, Headers, Response } from '@angular/http'

import { Observable, Subject } from "rxjs";
import { API_MAIN, PACKAGE, IMAGE } from '../const/const.config';

import { Package } from '../model/package';

import 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/operator/delay';
import 'rxjs/add/operator/catch';
import 'rxjs/operator/mergeMap';
import 'rxjs/operator/switchMap';


@Injectable()
export class PackageService {
    constructor(private _http: Http, private _jsonp: Jsonp) {

    }

    getAllPackage(type: string) {
        const url = API_MAIN + PACKAGE + "?type=" + type;
        return this._http
            .get(url, this.getHeaders())
            .map(response => this.getPackageList(response.json()))
            .catch(err => {
                return Observable.throw(err);
            });

    }

    getPackageById(id: string): Observable<any> {
        const url = API_MAIN + PACKAGE + "/" + id;
        console.log(url);
        return this._http
            .get(url, this.getHeaders())
            .map(response => Package.forDetailPage(response.json()['results']))
            .catch(err => {
                return Observable.throw(err);
            })
    }

    private getHeaders() {
        let headers = new Headers();
        headers.append('Content-types', 'application/json; charset=utf-8');;
        return headers;
    }

    getPackageList(json: any): Package[] {
        return json['results'].map(data => Package.forListing(data));
    }

    // getPackage(json: any): Package {
    //     console.log(json['results']);
    //     return Package.formArray(json['results']);
    //     // return json['results'].map(data => console.log(data));
    // }
}


