import { Component, OnInit, Input } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { Package } from '../../shared/model/package';
import { PackageService } from '../../shared/service/package.service';

@Component({
  selector: 'gop-package-detail',
  templateUrl: './package-detail.component.html',
  styleUrls: ['./package-detail.component.scss']
})
export class PackageDetailComponent implements OnInit {


  myPackage: any = {};
  jsonData = {
    packages: [
      {
        name: "ထားဝယ် ၊ ကန်ချနပူရီ ၊ ဘန်ကောက်",
        date: "12/22/2017",
        duration: "3 Night / 4 Days",
        price: "140,000 MMK"
      },
      {
        name: "ထားဝယ် ၊ ကန်ချနပူရီ ၊ ဘန်ကောက်",
        date: "12/22/2017",
        duration: "3 Night / 4 Days",
        price: "140,000 MMK"
      },
      {
        name: "ထားဝယ် ၊ ကန်ချနပူရီ ၊ ဘန်ကောက်",
        date: "12/22/2017",
        duration: "3 Night / 4 Days",
        price: "140,000 MMK"
      }
    ]
  }


  id: string;

  relatedPackages = [];
  constructor(private _router: Router,
    private _route: ActivatedRoute,
    private _service: PackageService) {

    this.relatedPackages = this.jsonData.packages;

    this._route.params.subscribe(p => {
      this.id = p['id'];
      this._service.getPackageById(this.id).subscribe(data => this.myPackage = data);
    });
  }

  ngOnInit() {

  }

  getMyStyles() {
    let myStyles = {
      'background-image': this.myPackage.image_url ? 'url(' + this.myPackage.image_url + ')' : 'url(http://www.go-myanmar.com/sites/go-myanmar.com/files/uploads/img/andaman_resort_1_1.jpg)',
    };
    return myStyles;
  }
}



