import { Directive, HostListener, HostBinding, ElementRef, Renderer, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';

@Directive({
  selector: '[windowscroll]'
})
export class WindowScrollDirective {

  private className = 'top-nav-collape';
  private isScroll = false;

  @HostListener('window:scroll') startScroll() {
    let number = this.document.body.scrollTop;
    if (number > 100) {
      this.isScroll = true;
    } else {
      this.isScroll = false;
    }
  }

  @HostBinding('class.className') get setScroll() {
    return this.renderer.setElementClass(this.elementRef.nativeElement, this.className, this.isScroll);
  }



  constructor( @Inject(DOCUMENT) private document: any,
    private elementRef: ElementRef,
    private renderer: Renderer) { }

}



