import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'gop-package',
  templateUrl: './package.component.html',
  styleUrls: ['./package.component.scss']
})
export class PackageComponent implements OnInit {

  @Input() package;
  constructor(private _route: ActivatedRoute,
    private _router: Router) { }

  ngOnInit() {
  }

  getMyStyles() {
    let myStyles = {
      'background-image': this.package['image_url'] ? 'url(' + this.package['image_url'] + ')' : 'url(http://www.go-myanmar.com/sites/go-myanmar.com/files/uploads/img/andaman_resort_1_1.jpg)',
    };
    return myStyles;
  }

  gotoDeatil(id: string) {
    console.log(id);

    this._router.navigate(['/packages', id]);
  }
}



